.exampleModal{
    .modal-dialog{
        max-width: 800px;
    }
    .modal-content{
        padding: 30px;
        @media screen and (max-width:576px){
            padding: 0;
        }
    }
    font-family: 'Futura PT', sans-serif;
    color: #000;
    .modal-header{
        flex-direction: column;
        align-items: center;
        border-bottom: 0;
    }
    .modal-footer{
        border-top: 0;
    }
        h5{
        display: block;
        margin-bottom: 28px;
        font-size: 26px;
        line-height: 30px;
        font-weight: 900;
    }
    span{
        display: block;
        text-align: center;
        font-size: 18px;
        line-height: 21px;
        font-weight: 500;
    }
    input, button:not(.close){
        height: 44px;
        width: 100%;
        max-width: 250px;
        margin: 0 auto;
        margin-bottom: 18px;
        font-size: 16px;
        line-height: 44px;
        font-weight: 500;
        background: transparent;
        border: 2px solid #c9c7c3;
        border-radius:22px;
        box-sizing: border-box;
        box-shadow: none;
        &:hover{
            box-shadow: none;
        }
        &:focus{
            border: 2px solid #c9c7c3;
            box-shadow: none;
        }
    }
    button:not(.close){
        display: block;
        margin:0 auto;
        line-height: 20px;
        border: none;
        color: #fff;
        background-color: #0090ff;
    }
    p{
        display: block;
        padding: 0 20%;
        text-align: center;
        font-size: 14px;
        line-height: 17px;
        @media screen and (max-width:576px){
            padding: 0;
        }
    }
    .close{
        position: absolute;
        top: 15px;
        right: 20px;
        opacity: 1;
        span{
            font-size: 30px;
            text-shadow: none;
        }
    }
}